<template><undefined>

<h1>Tooltip 文字提示</h1>
<p>简单的文字提示气泡框</p>
<h2 id="he-shi-shi-yong"><a class="header-anchor" href="#he-shi-shi-yong" aria-hidden="true">¶</a> 何时使用</h2>
<p>鼠标移入则显示提示，移出消失，气泡浮层不承载复杂文本和操作。<br>
可用来代替系统默认的 <code v-pre="">title</code> 提示，提供一个<code v-pre="">按钮/文字/操作</code>的文案解释。</p>
<h2 id="dai-ma-yan-shi"><a class="header-anchor" href="#dai-ma-yan-shi" aria-hidden="true">¶</a> 代码演示</h2>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;v-tooltip content=\&quot;提示文本\&quot;&gt;\n    &lt;span&gt;鼠标一上来就会出现&lt;/span&gt;\n&lt;/v-tooltip&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><v-tooltip content="提示文本">
    <span>鼠标一上来就会出现</span>
</v-tooltip>
</div>
                  <div slot="description"><h4 id="ji-ben"><a class="header-anchor" href="#ji-ben" aria-hidden="true">¶</a> 基本</h4>
<p>最简单的用法</p>
</div>
                  <div class="highlight" slot="code"><pre><code class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-tooltip</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"提示文本"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>鼠标一上来就会出现<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">v-tooltip</span>&gt;</span>
</code></pre>
</div>
                <summary>
  #### 基本
  最简单的用法
</summary>
<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-tooltip</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"提示文本"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">span</span>&gt;</span>鼠标一上来就会出现<span class="hljs-tag">&lt;/<span class="hljs-name">span</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">v-tooltip</span>&gt;</span>
</code></pre>
</demo-box>
<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;template&gt;\n    &lt;div class=\&quot;box\&quot;&gt;\n        &lt;div class=\&quot;top\&quot;&gt;\n            &lt;v-tooltip placement=\&quot;topLeft\&quot; controlled&gt;\n                &lt;v-button&gt;上左&lt;/v-button&gt;\n                &lt;template slot=\&quot;content\&quot;&gt;\n                    &lt;p&gt;topLeft 文字提示&lt;/p&gt;\n                    &lt;p&gt;topLeft 文字提示&lt;/p&gt;\n                    &lt;p&gt;topLeft 文字提示&lt;/p&gt;\n                &lt;/template&gt;\n            &lt;/v-tooltip&gt;\n            &lt;v-tooltip content=\&quot;top 文字提示,初始可见\&quot; placement=\&quot;top\&quot; init-visible&gt;\n                &lt;v-button&gt;上边&lt;/v-button&gt;\n            &lt;/v-tooltip&gt;\n            &lt;v-tooltip content=\&quot;topRight 不可用tooltip\&quot; placement=\&quot;topRight\&quot; disabled&gt;\n                &lt;v-button&gt;上右&lt;/v-button&gt;\n            &lt;/v-tooltip&gt;\n        &lt;/div&gt;\n        &lt;div class=\&quot;left\&quot;&gt;\n            &lt;v-tooltip content=\&quot;leftTop 文字提示\&quot; placement=\&quot;leftTop\&quot; @hide=\&quot;popperHide\&quot;&gt;\n                &lt;v-button&gt;左上&lt;/v-button&gt;\n            &lt;/v-tooltip&gt;\n            &lt;v-tooltip content=\&quot;left 文字提示\&quot; placement=\&quot;left\&quot;&gt;\n                &lt;v-button&gt;左边&lt;/v-button&gt;\n            &lt;/v-tooltip&gt;\n            &lt;v-tooltip content=\&quot;leftBottom 文字提示\&quot; placement=\&quot;leftBottom\&quot;&gt;\n                &lt;v-button&gt;左下&lt;/v-button&gt;\n            &lt;/v-tooltip&gt;\n        &lt;/div&gt;\n\n        &lt;div class=\&quot;right\&quot;&gt;\n            &lt;v-tooltip content=\&quot;rightTop 文字提示\&quot; placement=\&quot;rightTop\&quot;&gt;\n                &lt;v-button&gt;右上&lt;/v-button&gt;\n            &lt;/v-tooltip&gt;\n            &lt;v-tooltip content=\&quot;right 文字提示\&quot; placement=\&quot;right\&quot;&gt;\n                &lt;v-button&gt;右边&lt;/v-button&gt;\n            &lt;/v-tooltip&gt;\n            &lt;v-tooltip content=\&quot;rightBottom 文字提示\&quot; placement=\&quot;rightBottom\&quot;&gt;\n                &lt;v-button&gt;右下&lt;/v-button&gt;\n            &lt;/v-tooltip&gt;\n        &lt;/div&gt;\n        &lt;div class=\&quot;bottom\&quot;&gt;\n            &lt;v-tooltip content=\&quot;bottomLeft 文字提示\&quot; placement=\&quot;bottomLeft\&quot;&gt;\n                &lt;v-button&gt;下左&lt;/v-button&gt;\n            &lt;/v-tooltip&gt;\n            &lt;v-tooltip content=\&quot;bottom 文字提示\&quot; placement=\&quot;bottom\&quot;&gt;\n                &lt;v-button&gt;下边&lt;/v-button&gt;\n            &lt;/v-tooltip&gt;\n            &lt;v-tooltip content=\&quot;bottomRight 文字提示\&quot; placement=\&quot;bottomRight\&quot;&gt;\n                &lt;v-button&gt;下右&lt;/v-button&gt;\n                &lt;div slot=\&quot;content\&quot;&gt;\n                    &lt;span style=\&quot;color:#AAA\&quot;&gt;自定义提示内容&lt;/span&gt;\n                &lt;div&gt;\n            &lt;/div&gt;&lt;/div&gt;&lt;/v-tooltip&gt;\n        &lt;/div&gt;\n    &lt;/div&gt;\n&lt;/template&gt;\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n    methods: {\n        popperHide() {\n            console.log('tooltip hide');\n        }\n    }\n  }\n&quot;,&quot;style&quot;:&quot;\n    .box {\n        width: 500px;\n    }\n    .top {\n        margin-left: 125px;\n    }\n    .left {\n        float: left;\n        width: 60px;\n    }\n    .right {\n        width: 60px; \n        margin-left: 380px;\n    }\n    \n    .bottom {\n        clear: both;\n        margin-left: 125px;\n    }\n    .box-demo .ant-tooltip-wrapper {\n        margin-right: 8px;\n        margin-bottom: 8px;\n    }\n&quot;}">
                  <div class="box-demo-show" slot="component"><template>
    <div class="box">
        <div class="top">
            <v-tooltip placement="topLeft" controlled="">
                <v-button>上左</v-button>
                <template slot="content">
                    <p>topLeft 文字提示</p>
                    <p>topLeft 文字提示</p>
                    <p>topLeft 文字提示</p>
                </template>
            </v-tooltip>
            <v-tooltip content="top 文字提示,初始可见" placement="top" init-visible="">
                <v-button>上边</v-button>
            </v-tooltip>
            <v-tooltip content="topRight 不可用tooltip" placement="topRight" disabled>
                <v-button>上右</v-button>
            </v-tooltip>
        </div>
        <div class="left">
            <v-tooltip content="leftTop 文字提示" placement="leftTop" @hide="popperHide">
                <v-button>左上</v-button>
            </v-tooltip>
            <v-tooltip content="left 文字提示" placement="left">
                <v-button>左边</v-button>
            </v-tooltip>
            <v-tooltip content="leftBottom 文字提示" placement="leftBottom">
                <v-button>左下</v-button>
            </v-tooltip>
        </div>

        <div class="right">
            <v-tooltip content="rightTop 文字提示" placement="rightTop">
                <v-button>右上</v-button>
            </v-tooltip>
            <v-tooltip content="right 文字提示" placement="right">
                <v-button>右边</v-button>
            </v-tooltip>
            <v-tooltip content="rightBottom 文字提示" placement="rightBottom">
                <v-button>右下</v-button>
            </v-tooltip>
        </div>
        <div class="bottom">
            <v-tooltip content="bottomLeft 文字提示" placement="bottomLeft">
                <v-button>下左</v-button>
            </v-tooltip>
            <v-tooltip content="bottom 文字提示" placement="bottom">
                <v-button>下边</v-button>
            </v-tooltip>
            <v-tooltip content="bottomRight 文字提示" placement="bottomRight">
                <v-button>下右</v-button>
                <div slot="content">
                    <span style="color:#AAA">自定义提示内容</span>
                <div>
            </div></div></v-tooltip>
        </div>
    </div>
</template>


</div>
