<demo-box :jsfiddle="{&quot;html&quot;:&quot;&lt;v-tooltip content=\&quot;延时1秒展示\&quot; placement=\&quot;topLeft\&quot; :delay=\&quot;1000\&quot;&gt;\n    &lt;v-button&gt;延时展示&lt;/v-button&gt;\n&lt;/v-tooltip&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                  <div class="box-demo-show" slot="component"><v-tooltip content="延时1秒展示" placement="topLeft" :delay="1000">
