<pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">v-tooltip</span> <span class="hljs-attr">content</span>=<span class="hljs-string">"延时1秒展示"</span> <span class="hljs-attr">placement</span>=<span class="hljs-string">"topLeft"</span> <span class="hljs-attr">:delay</span>=<span class="hljs-string">"1000"</span>&gt;</span>
