<h2 id="api"><a class="header-anchor" href="#api" aria-hidden="true">¶</a> API</h2>
<h3 id="tooltip-props"><a class="header-anchor" href="#tooltip-props" aria-hidden="true">¶</a> Tooltip Props</h3>
<table class="table"><thead>
<tr>
<th>参数</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>placement</td>
<td>气泡框位置，可选 <code v-pre="">top</code> <code v-pre="">left</code> <code v-pre="">right</code> <code v-pre="">bottom</code> <code v-pre="">topLeft</code> <code v-pre="">topRight</code> <code v-pre="">bottomLeft</code> <code v-pre="">bottomRight</code> <code v-pre="">leftTop</code> <code v-pre="">leftBottom</code> <code v-pre="">rightTop</code> <code v-pre="">rightBottom</code></td>
<td>String</td>
<td>top</td>
</tr>
<tr>
<td>content</td>
<td>显示的内容</td>
<td>String/Number</td>
<td>-</td>
</tr>
<tr>
<td>content:slot</td>
<td>显示的内容</td>
<td>Slot Node</td>
<td>-</td>
</tr>
<tr>
<td>init-visible</td>
<td>初始状态是否显示tooltip</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>delay</td>
<td>鼠标移入后延时多少才显示tooltip,单位:毫秒</td>
<td>Number</td>
<td>0</td>
</tr>
<tr>
<td>disabled</td>
<td>tooltip是否可用</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>controlled</td>
<td>保持tooltop显示</td>
<td>Boolean</td>
<td>false</td>
</tr>
</tbody>
</table>
<h3 id="tooltip-events"><a class="header-anchor" href="#tooltip-events" aria-hidden="true">¶</a> Tooltip Events</h3>
<table class="table"><thead>
<tr>
<th>事件</th>
<th>说明</th>
<th>参数</th>
</tr>
</thead>
<tbody>
<tr>
<td>hide</td>
<td>tooptip由显示变为隐藏时触发</td>
<td>-</td>
</tr>
</tbody>
</table>
</undefined></template>
<style scoped>
    .box {
        /* width: 500px; */
    }
    .top {
        margin-left: 125px;
    }
    .left {
        float: left;
        width: 60px;
    }
    .right {
        width: 60px; 
        margin-left: 380px;
    }
    .bottom {
        clear: both;
        margin-left: 125px;
    }
    .box-demo .ant-tooltip-wrapper {
        margin-right: 8px;
        margin-bottom: 8px;
    }
</style>
<script>

export default {
    methods: {
        popperHide() {
            console.log('tooltip hide');
        }
    }
}
</script>


